* {
    box-sizing: border-box;
}
body {
    font-family: 'Open Sans' sans-serif;
    margin: 0;
}
.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: lightgray;
    padding: 1em;
    height: 30vh;

    &__title {
        font-size: 2em;
        padding-bottom: 0.25em;

        @media (min-width: 900px){
            font-size: 3em;
        }
    }

    &__text {
        text-align: center;
        line-height: 130%;
    }
}
.main {
    padding: 2em;
    border: 1px solid blue;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 1em;
    background-color: lightblue;

    @media (min-width: 900px) {
        margin-top: 2em;
    }

    @media (min-width: 1200px) {
        margin-top: 3em;
    }
}
.content {
    margin: 1em;
    border: 1px solid red;
}
.content__container {
    align-items: center;
    width: 400px;
    background-color: white;
}
.content__picture {
    max-width: 400px;
    align-items: center;
}
.content__text {
    text-align: center;
    line-height: 120%;
    padding: 1em;
}

